:root {
  --ins-color--orange: #ec7a08; }

button:focus {
  outline: none; }

.ins-c-inventory__detail--edit {
  min-width: 500px;
  min-width: 31.25rem; }

.ins-inventory-detail.pf-c-page-header.pf-c-page__main-section {
  padding-bottom: 0; }

.ins-c-inventory__detail--header .pf-l-split__item:last-child {
  min-width: 100px;
  min-width: 6.25rem; }

.ins-c-inventory__detail--action {
  padding-left: var(--pf-global--spacer--sm); }

.ins-c-inventory-detail__app-tabs ul:before {
  content: '';
  border-right: 1px solid var(--pf-c-tabs__item--BorderColor); }

.ins-c-inventory-detail__app-tabs ul:after {
  content: '';
  width: 100%;
  margin-top: var(--pf-c-content--li--MarginTop);
  border-left: 1px solid var(--pf-c-tabs__item--BorderColor); }

.ins-c-inventory-detail__app-tabs ul li button:before {
  border-bottom: none; }

.ins-entity-detail .ins-entity-facts div {
  margin: 5px;
  margin: 0.3125rem;
  margin-left: 0; }
  .ins-entity-detail .ins-entity-facts div span:first-child {
    margin-right: 5px;
    margin-right: 0.3125rem; }
  .ins-entity-detail .ins-entity-facts div .ins-c-skeleton {
    display: inline-block; }

.ins-entity-detail .ins-entity-tags svg {
  position: relative;
  top: 3px; }

.ins-entity-detail .ins-entity-tags > div > span {
  margin: 5px; }

.ins-entity-detail .ins-tab-layout {
  margin-top: 20px; }

@media screen and (min-width: 768px) {
  .ins-entity-detail .ins-entity-facts div.pf-l-grid__item:nth-child(even), .ins-entity-detail .ins-entity-header div.pf-l-grid__item:nth-child(even) {
    text-align: right; } }
